import './MyCollection.css';
import React from 'react';
import Header from './../../../Header/Header';
import dummy from '../../../../media/images/dummy.png';
import { connect } from 'react-redux';

class MyCollection extends React.Component {
  componentDidUpdate() {
    if (!window.gapi.auth2.getAuthInstance().isSignedIn.le) {
      window.open(
        `${window.location.protocol}//${window.location.host}/NotLogged`,
        '_self'
      );
    }
  }

  render() {
    return (
      <div>
        <Header />
        <div>
          <div className="collection-title">My Collection</div>
          <div className="collection-content-container">
            <div className="ui items collection-content">
              <div className="item">
                <div className="ui small image">
                  <img src={dummy} alt="" />
                </div>
                <div className="middle aligned content">
                  <div className="header">Content A</div>
                  <div className="description">
                    <p></p>
                  </div>
                  <div className="extra">
                    <div className="ui right floated button">Action</div>
                  </div>
                </div>
              </div>
              <div className="item">
                <div className="ui small image">
                  <img src={dummy} alt="" />
                </div>
                <div className="middle aligned content">
                  <div className="header">Content B</div>
                  <div className="description">
                    <p></p>
                  </div>
                  <div className="extra">
                    <div className="ui right floated button">Action</div>
                  </div>
                </div>
              </div>
              <div className="item">
                <div className="ui small image">
                  <img src={dummy} alt="" />
                </div>
                <div className="middle aligned content">
                  <div className="header">Content C</div>
                  <div className="description">
                    <p></p>
                  </div>
                  <div className="extra">
                    <div className="ui right floated button">Action</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect()(MyCollection);
