import history from './../history';
import models from './../apis/models';
import users from './../apis/users';
import axios from 'axios';

export const createModel = (formValues, classification, formatArr, images) => {
  return async (dispatch) => {
    formValues.modelPrice = parseInt(formValues.modelPrice);
    formValues = {
      ...formValues,
      modelClassification: classification,
      modelFormats: formatArr,
      modelImages: images,
    };

    const response = await models.post('/', formValues);
    console.log('POST REQUEST SUCCESSFULL');
    console.log(response);

    dispatch({
      type: 'CREATE_MODEL',
      payload: response.data.data.newModel._id,
    });
  };
};

export const editModel = (
  formValues,
  classification,
  formatArr,
  images,
  files,
  id
) => {
  return async (dispatch) => {
    formValues.modelPrice = parseInt(formValues.modelPrice);

    if (!files) {
      formValues = {
        ...formValues,
        modelClassification: classification,
        modelFormats: formatArr,
        modelImages: images,
        _id: id,
      };
    } else {
      formValues = {
        ...formValues,
        modelClassification: classification,
        modelFormats: formatArr,
        modelImages: images,
        modelPreview: files[0],
        _id: id,
      };
    }

    const response = await models.patch(`/${id}`, formValues);

    dispatch({ type: 'CREATE_MODEL', payload: response.data });
    history.push('/');
  };
};

export const imagesToUpload = (images) => {
  return async (dispatch) => {
    dispatch({ type: 'IMAGES_TO_UPLOAD', payload: images });
  };
};

export const imagesToSubmit = (imgArr) => {
  return async (dispatch) => {
    dispatch({ type: 'IMAGES_TO_SUBMIT', payload: imgArr });
  };
};

export const filesToUpload = (files) => {
  return async (dispatch) => {
    dispatch({ type: 'FILES_TO_UPLOAD', payload: files });
  };
};

export const filesToSubmit = (fileArr) => {
  return async (dispatch) => {
    dispatch({ type: 'FILES_TO_SUBMIT', payload: fileArr });
  };
};

export const getModels = () => {
  // http://localhost:8000/api/v1/model
  // https://hushpuppys-3d-hub-api.herokuapp.com/api/v1/models
  return async (dispatch) => {
    const response = await axios.get('http://localhost:8000/api/v1/models');

    dispatch({ type: 'GET_MODELS', payload: response.data.data });
  };
};

export const getModel = (id) => {
  return async (dispatch) => {
    const response = await models.get(`/${id}`);

    dispatch({ type: 'GET_MODEL', payload: response.data.data });
  };
};

export const selectModel = (model) => {
  return async (dispatch) => {
    dispatch({
      type: 'SELECTED_MODEL',
      payload: model,
    });
  };
};

export const selectedPreviewModel = (model) => {
  return async (dispatch) => {
    dispatch({ type: 'SELECTED_PREVIEW_MODEL', payload: model });
  };
};

export const deleteModel = (id) => {
  return async (dispatch) => {
    const response = await models.delete(`/${id}`);

    dispatch({ type: 'DELETE_MODEL', payload: response.data });
    history.push('/');
  };
};

export const changeAccountButtonState = (state) => {
  return async (dispatch) => {
    if (!state) {
      state = true;
    } else {
      state = false;
    }
    dispatch({ type: 'CHANGE_ACCOUNT_BUTTON_STATE', payload: state });
  };
};

export const checkIfUserIsRegistered = (userData) => {
  return async (dispatch) => {
    const response = await users.get(`/${userData.NT}`);

    if (!response.data.regStatus) {
      await users.post('/', userData);
    }

    dispatch({
      type: 'USER_CHECK',
      payload: {
        status: true,
        userData: { gId: userData.NT, name: userData.tV },
      },
    });
  };
};

export const checkLoggedState = () => {
  return async (dispatch) => {
    if (window.gapi.auth2.getAuthInstance().isSignedIn.le) {
      dispatch({
        type: 'CHECK_LOG_STATE',
        payload: {
          status: true,
          userData: {
            gId: window.gapi.auth2.getAuthInstance().currentUser.le.rt.NT,
            name: window.gapi.auth2.getAuthInstance().currentUser.le.rt.tV,
          },
        },
      });
    } else {
      dispatch({
        type: 'CHECK_LOG_STATE',
        payload: {
          status: false,
          userData: null,
        },
      });
    }
  };
};
